import request from '../shared/faceMatchRequest';


function processFaceMatch(data) {
    return request({
        url: '/api/v1/facematch',
        method: 'POST',
        data: data
    });
}



function processLiveness(data) {
    console.log("process liveness")
    return request({
        url: '/api/v1/liveness',
        method: 'POST',
        data: data
    });
}

const FaceMatchService = {
    processFaceMatch,
    processLiveness
};

export default FaceMatchService;